.energy-track-container {
  background:url("../shared/images/Background.png");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
    z-index: 0;
    min-height: 100vh;
    padding: 24px;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    color: #ffffff;
    position: relative;
    overflow: hidden;
  }
  
  
  
  
  
  
  .energy-track-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    background: rgba(59, 130, 246, 0.08);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(59, 130, 246, 0.15);
    border-bottom: 1px solid rgba(59, 130, 246, 0.2);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
  }
  
  .header-left h1 {
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
    margin: 0;
  }
  .header-right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
  }
  .stat-cards-container {
    display: flex;
    gap: 16px;
    align-items: center;
  }
  .stat-card{
    min-width: 120px;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  }
  .stat-card .stat-value {
    font-size: 18px;
    font-weight: 700;
    color: #ffffff;
    margin: 4px 0;
    line-height: 1.2;
  }
  .stat-icon {
    color: #4CAF50;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .stat-card .stat-title {
    font-size: 12px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.7);
    margin: 0;
    line-height: 1.2;
  }
  
  .stat-icon svg {
    width: 20px;
    height: 20px;
  }
  @media (max-width: 1200px) {
    .stat-cards-container {
      gap: 12px;
    }
    
    .stat-card {
      min-width: 150px;
      padding: 12px;
    }
    
    .stat-value {
      font-size: 16px;
    }
  }
  
  @media (max-width: 768px) {
    .energy-track-header {
      flex-direction: column;
      gap: 16px;
      align-items: flex-start;
    }
    
    .header-right {
      width: 100%;
      justify-content: center;
    }
    
    .stat-cards-container {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 12px;
      width: 100%;
    }
    
    .stat-card {
      min-width: unset;
    }
  }

  .energy-track-header h1 {
    font-size: 2.5rem;
    font-weight: 700;
    color: #ffffff;
    margin: 0;
    text-align: center;
    z-index: 3;
  }
  
  /* Sub Navigation with Frosted Glass Effect */
  .sub-nav-container {
    position: relative;;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 12px 20px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
    z-index: 2;
  }
  
  .sub-nav-bar {
    display: flex;
    gap: 8px;
    z-index: 2;
  }
  
  .sub-nav-item {
    padding: 12px 24px;
    background: transparent;
    border: none;
    border-radius: 12px;
    color: rgba(255, 255, 255, 0.7);
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.3s ease;
    text-transform: capitalize;
  }
  
  .sub-nav-item:hover {
    color: rgba(255, 255, 255, 0.9);
    background: rgba(59, 130, 246, 0.1);
  }
  
  .sub-nav-item.active {
    background: rgba(255, 255, 255, 0.3);
    color: #fff;
    box-shadow: 0 4px 16px rgba(59, 130, 246, 0.2);
  }
  
  /* Time Range Dropdown */
  .time-range-dropdown {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .time-range-select {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    padding: 12px 40px 12px 16px;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    min-width: 140px;
    transition: all 0.3s ease;
  }
  
  .time-range-select:hover {
    background: rgba(255, 255, 255, 0.15);
    border-color: rgba(59, 130, 246, 0.4);
  }
  
  .time-range-select:focus {
    color: #0f172a;
    outline: none;
    border-color: rgba(59, 130, 246, 0.6);
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
  
  .dropdown-arrow {
    position: absolute;
    right: 12px;
    pointer-events: none;
    color: rgba(255, 255, 255, 0.7);
  }
  
  /* Power Usage Section */
  .power-usage-section {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  
  /* Three Graph Containers */
  .graphs-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: 24px;
    z-index: 3;
  }
  
  .graph-card {
    background: rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 20px;
    padding: 24px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
  }
  
  .graph-card:hover {
    background: rgba(255, 255, 255, 0.12);
    border-color: rgba(59, 130, 246, 0.3);
    transform: translateY(-2px);
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  }
  
  .graph-card h3 {
    font-size: 1.2rem;
    font-weight: 600;
    color: #ffffff;
    margin: 0 0 20px 0;
    text-align: center;
  }
  
  .chart-container {
    width: 100%;
    height: 220px;
    margin-top: 16px;
  }
  
  /* Generate Report Section */
  .generate-report-section {
    background: rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 20px;
    padding: 32px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
    margin-top: 24px;
    z-index: 3;
  }
  
  .generate-report-section h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
    margin: 0 0 24px 0;
    text-align: center;
  }
  
  .report-controls {
    display: flex;
    gap: 20px;
    align-items: end;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .report-control {
    display: flex;
    flex-direction: column;
    gap: 8px;
    min-width: 180px;
  }
  
  .report-control label {
    font-size: 14px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.8);
  }
  
  .report-select {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    padding: 12px 16px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    transition: all 0.3s ease;
  }
  
  .report-select:hover {
    background: rgba(255, 255, 255, 0.15);
    border-color: rgba(59, 130, 246, 0.4);
  }
  
  .report-select:focus {
    color: #0f172a;
    outline: none;
    border-color: rgba(59, 130, 246, 0.6);
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
  
  .generate-report-btn {
    background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
    border: none;
    border-radius: 12px;
    padding: 14px 32px;
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 16px rgba(59, 130, 246, 0.3);
    height: fit-content;
  }
  
  .generate-report-btn:hover {
    background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(59, 130, 246, 0.4);
  }
  
  .generate-report-btn:active {
    transform: translateY(0);
  }
  
  /* Section Container */
  .section-container {
    animation: fadeIn 0.5s ease-in-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Section Content for Other Tabs */
  .section-content {
    background: rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 20px;
    padding: 40px;
    text-align: center;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
  }
  
  .section-content h2 {
    font-size: 2rem;
    font-weight: 600;
    color: #ffffff;
    margin: 0 0 16px 0;
  }
  
  .section-content p {
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.7);
    margin: 0;
  }
  .recent-reports-section {
    background: rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 20px;
    padding: 32px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
    margin-top: 24px;
    z-index: 3;
  }
  
  .recent-reports-section h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
    margin: 0 0 24px 0;
    text-align: center;
  }
  
  .reports-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  .report-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 16px 20px;
    transition: all 0.3s ease;
  }
  
  .report-item:hover {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(59, 130, 246, 0.3);
    transform: translateY(-2px);
  }
  
  .report-info h4 {
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
    margin: 0 0 4px 0;
  }
  
  .report-info p {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.6);
    margin: 0;
  }
  
  .download-btn {
    background: rgba(59, 130, 246, 0.2);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(59, 130, 246, 0.3);
    border-radius: 8px;
    padding: 8px 16px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 6px;
  }
  
  .download-btn:hover {
    background: rgba(59, 130, 246, 0.3);
    border-color: rgba(59, 130, 246, 0.5);
    transform: translateY(-1px);
  }
  
  .download-icon {
    width: 14px;
    height: 14px;
  }
  
  @media (max-width: 768px) {
    .report-item {
      flex-direction: column;
      gap: 12px;
      align-items: flex-start;
    }
    
    .download-btn {
      align-self: flex-end;
    }
  }
  
  /* Responsive Design */
  @media (max-width: 1200px) {
    .graphs-container {
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    }
  }
  
  @media (max-width: 768px) {
    .energy-track-container {
      padding: 16px;
    }
    
    .energy-track-header h1 {
      font-size: 2rem;
    }
    
    .sub-nav-container {
      flex-direction: column;
      gap: 16px;
      padding: 16px;
    }
    
    .sub-nav-bar {
      justify-content: center;
      flex-wrap: wrap;
    }
    
    .graphs-container {
      grid-template-columns: 1fr;
    }
    
    .report-controls {
      flex-direction: column;
      align-items: center;
    }
    
    .report-control {
      width: 100%;
      max-width: 300px;
    }
  }